import React, { PropsWithChildren } from "react";
import styled from "styled-components";

import { spacingMap } from "../styleguide";

import { StyledModalProps } from "./Modal.types";
import { useModalContext } from "./ModalContext";

export interface ModalBodyProps {
	dataQA: string
	className?: string
}

const StyledModalBody = styled.div<StyledModalProps>`
	max-height: 320px;
	padding: ${({ variant }) => variant === "scrollable" ? `${spacingMap.sm}` : 0} ${spacingMap.md};
	overflow-x: hidden;
	overflow-y: ${({ variant }) => variant === "scrollable" ? "auto" : "hidden"};
`;

const ModalBody = ({
	children,
	dataQA,
	className,
}: PropsWithChildren<ModalBodyProps>) => {
	const { variant } = useModalContext();
	return (
		<StyledModalBody
			variant={variant}
			data-qa={`${dataQA}-modal-body`}
			className={className}
		>
			{children}
		</StyledModalBody>
	);
};

ModalBody.displayName = "ModalBody";
export default ModalBody;
